import React from "react";
import { createGlobalStyle } from "styled-components";

import MobileLandscape from "./views/MobileLandscape";
import MobilePortrait from "./views/MobilePortrait";
import SmallMobilePortrait from "./views/SmallMobilePortrait";
import TabletPortrait from "./views/TabletPortrait";
import TabletLandscape from "./views/TabletLandscape";
import Default from "./views/Default";

const GlobalStyle = createGlobalStyle`
  * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html,
body {
  min-height: 100vh;
  scroll-behavior: smooth;
}
`;

const App = () => (
  <React.Fragment>
    <Default />
    <MobileLandscape />
    <MobilePortrait />
    <SmallMobilePortrait />
    <TabletPortrait />
    <TabletLandscape />
    <GlobalStyle/>
  </React.Fragment>
);

export default App;
