import React from "react";
import styled, { keyframes } from "styled-components";
import Hamburger from "./Hamburger";
import Wrapper from "./Wrapper";
import Emoji from "react-emoji-render";

const fadein = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const StyledA = styled.a`
  text-decoration: none;
  color: ${props => props.color || "#646464"};
  font-weight: normal;
  font-size: 26px;
  padding: 2vh 2vw;
  margin: 2vh 2vw;
  border-left: 2px solid #646464;
`;

const HiddenMenu = styled.div`
  position: fixed;
  left: 0;
  top: 0vh;
  width: 100vw;
  min-height: 101vh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  animation: ${fadein} 0.4s;
`;

const HamburgerMenu = props => (
  <HiddenMenu>
    <Wrapper padding="5vw" flexDirection="row" justifyContent="flex-end">
      <span
        onClick={() => {
          props.hideHamburgerMenu();
        }}
      >
        <Hamburger />
      </span>
    </Wrapper>

    <Wrapper padding="5vw">
      <StyledA
        href="#about"
        onClick={() => {
          props.hideHamburgerMenu();
        }}
      >
        <Emoji text="💁‍♂️ O mnie" />
      </StyledA>

      <StyledA
        href="#skills"
        onClick={() => {
          props.hideHamburgerMenu();
        }}
      >
        <Emoji text="👨‍💻 Moje umiejętności" />
      </StyledA>

      <StyledA
        href="#contact"
        onClick={() => {
          props.hideHamburgerMenu();
        }}
      >
        <Emoji text="📱 Kontakt" />
      </StyledA>
    </Wrapper>
  </HiddenMenu>
);

export default HamburgerMenu;
