import React, { useState } from "react";
import styled from "styled-components";
import Hamburger from "./Hamburger";
import P from "./P";
import HamburgerMenu from "./HamburgerMenu";

const StyledNav = styled.nav`
  position: fixed;
  height: 10vh;
  width: 100vw;
  padding: 0 5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  color: #646464;
  font-size: ${props => props.fontSize + "px"};
  line-height: ${props => props.fontSize + 10 + "px"};
`;

const StyledA = styled.a`
  text-decoration: none;
  color: ${props => props.color || "#646464"};
  padding: 0;
`;

const StyledMenu = styled.div`
  flex: 2;
  letter-spacing: 0.105em;
  display: flex;
  justify-content: flex-end;
`;

const Navbar = props => {
  const [isMenuHidden, handleMenuClick] = useState(true);

  if (props.isHamburger)
    return (
      <StyledNav fontSize={props.fontSize} height={props.height}>
        <P color="#003d33">KACPER KRAWCZYK</P>
        {isMenuHidden ? (
          <span
            onClick={() => {
              handleMenuClick(false);
            }}
          >
            <Hamburger isMenuHidden={isMenuHidden} />
          </span>
        ) : (
          <HamburgerMenu
            hideHamburgerMenu={() => {
              handleMenuClick(true);
            }}
          />
        )}
      </StyledNav>
    );
  else
    return (
      <StyledNav fontSize={props.fontSize} height={props.height}>
        <StyledMenu>
          <StyledA href="#about" color="#003d33">
            KACPER KRAWCZYK
          </StyledA>
          <StyledA href="#skills">&nbsp;|&nbsp;O MNIE&nbsp;|&nbsp;</StyledA>
          <StyledA href="#contact">KONTAKT</StyledA>
        </StyledMenu>
      </StyledNav>
    );
};

export default Navbar;
