import React from "react";
import styled from "styled-components";
import Wrapper from "./Wrapper";
import P from "./P";

import img_github from "../images/img_github.png";
import img_gmail from "../images/img_gmail.png";
import img_facebook from "../images/img_facebook.png";

const SocialMediaIcon = styled.img`
  height: ${props => props.height || "10vw"};
`;

const StyledA = styled.a`
  width: 60%;
  display: flex;
  align-items: center;
  padding-top: 1vh;
  text-decoration: none;
  color: ${props => props.color || "#646464"};
`;

const SocialMediaBar = props => (
  <Wrapper
    flex={1}
    padding="2vh 0"
    justifyContent="space-around"
    flexDirection="column"
  >
    <StyledA href="http://github.com/kraw3k" aria-label="GitHub">
      <SocialMediaIcon src={img_github} height={props.iconHeight} />
      <P padding="0 20px" fontSize={props.fontSize}>
        github.com/kraw3k
      </P>
    </StyledA>
    <StyledA href="mailto:kacper5047@gmail.com" aria-label="Mail">
      <SocialMediaIcon src={img_gmail} height={props.iconHeight} />
      <P padding="0 20px" fontSize={props.fontSize}>
        kacper5047@gmail.com
      </P>
    </StyledA>
    <StyledA href="http://facebook.com/kraw3k" aria-label="Facebook">
      <SocialMediaIcon src={img_facebook} height={props.iconHeight} />
      <P padding="0 20px" fontSize={props.fontSize}>
        facebook.com/kraw3k
      </P>
    </StyledA>
  </Wrapper>
);

export default SocialMediaBar;
