import React from "react";
import Responsive from "react-responsive";

import Navbar from "../components/Navbar";
import SocialMediaBar from "../components/SocialMediaBar";
import Section from "../components/Section";
import Photo from "../components/Photo";
import Wrapper from "../components/Wrapper";
import H1 from "../components/H1";
import H2 from "../components/H2";
import H3 from "../components/H3";
import P from "../components/P";
import ColorAccent from "../components/ColorAccent";

import photo_about from "../images/photo_about_1.jpg";
import photo_skills from "../images/photo_skills_1.jpg";
import photo_contact from "../images/photo_contact_2.jpg";

const Default = props => (
  <Responsive {...props} minWidth={1201}>
    <Navbar isHamburger={false} fontSize={20} />

    {/* ABOUT */}
    <Section id="about" flexDirection="row">
      <Photo src={photo_about} />
      <Wrapper flex={2} marginLeft="5vw" justifyContent="space-evenly">
        <Wrapper>
          <H1 fontSize={128}>Cześć,</H1>
          <H2 fontSize={64}>
            nazywam się <ColorAccent>Kacper</ColorAccent>
          </H2>
        </Wrapper>

        <Wrapper>
          <H3 fontSize={40}>
            Jestem <ColorAccent>web developerem</ColorAccent>
          </H3>
          <P fontSize={24}>
            Ukończyłem krakowskie Technikum Łączności zdobywając tytuł
            <ColorAccent>&nbsp;technik informatyk</ColorAccent>
            .&nbsp;Zajmuję się tworzeniem aplikacji internetowych.
          </P>
        </Wrapper>
      </Wrapper>
    </Section>

    {/* SKILLS */}
    <Section id="skills" flexDirection="row">
      <Wrapper flex={2} marginRight="5vw" justifyContent="space-evenly">
        <Wrapper>
          <H1 fontSize={128}>Moje</H1>
          <H2 fontSize={64}>umiejętności</H2>
        </Wrapper>

        <Wrapper>
          <P fontSize={24}>
            Posiadam doświadczenie w pracy z m.in. React, Node.js, Express.js,
            PHP, MySQL, Firebase, MongoDB, NeDB, Three.js, React Native, PWA,
            SCSS, jQuery, Bootstrap, handlebars.
          </P>
        </Wrapper>
      </Wrapper>
      <Photo src={photo_skills} />
    </Section>

    {/* CONTACT */}
    <Section id="contact" flexDirection="row">
      <Photo src={photo_contact} />
      <Wrapper flex={2} marginLeft="5vw" justifyContent="space-evenly">
        <Wrapper>
          <H1 fontSize={128}>Kontakt</H1>
          <H2 fontSize={64}>porozmawiajmy!</H2>
        </Wrapper>

        <Wrapper>
          <P fontSize={28}>
            Zachęcam do zapoznania się z moimi projektami na GitHub.
          </P>
          <SocialMediaBar iconHeight="4vw" fontSize={24} />
        </Wrapper>
      </Wrapper>
    </Section>
  </Responsive>
);

export default Default;
